import { PropertiesService } from "@alis/ng-base";

export class FileServiceMetadata {
    domainId: string; //"aestests"
    traceType: string; //"file"
    objectId: string; //"45cb34a4-aec6-4d62-8acd-6cb361cff26d"
    subDomainId: string; //"filetest"
    timestamp: number; //1611233898259
    tags: string;  //null
    deletionDateTimestamp: number; //1611233941300
    link: string; //""
    entryType: string; //FILE or DIRECTORY
    creationDate: string; //"2021-01-21T12:58:18.259977700Z"
    type: string; //doc
    creationDateTimestamp: number; //1611233898259
    createdAt: string; //"2021-01-09T17:00:00-03:00"
    path: Array<string>; //["aestests", "filetest"]
    lastAccessBy: string; //"Oberdan"
    size: number; //"1763738"
    createdBy: string; //"Oberdan"
    deletionDate: string; //"2021-01-21T12:59:01.300895Z"
    lastAccessDate: string; //"2021-01-21T12:58:52.505725100Z"
    name: string; //"Arquivo 77"
    location: string; //"/aestests/filetest/"
    lastAccessDateTimestamp: number; //1611233932505
    state: string; //"REMOVED"

    static fromFile(domainId: string,file: File, user?: string): FileServiceMetadata {
        let fileServiceMetadata = new FileServiceMetadata();
        fileServiceMetadata.domainId = domainId;
        fileServiceMetadata.subDomainId = "users";
        fileServiceMetadata.name = file.name;
        fileServiceMetadata.type = file.name.substring(file.name.lastIndexOf(".")+1);
        fileServiceMetadata.timestamp = Date.now();
        fileServiceMetadata.tags = null;
        fileServiceMetadata.entryType = "FILE";
        fileServiceMetadata.size = file.size;
        fileServiceMetadata.path = [ 
            fileServiceMetadata.domainId,
            fileServiceMetadata.subDomainId,
            user
        ]
        return fileServiceMetadata;
    }

    isFolder(): boolean {
        return "DIRECTORY" === this.entryType;
    }
}