import { EventService, PropertiesService, StateService, Trace, Event, TrackingWebsocketService, EventWebsocketService, TrackingService } from '@alis/ng-base';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import * as moment from 'moment';
import { forkJoin, Observable } from 'rxjs';
import { delay, map, repeat, retryWhen, filter } from 'rxjs/operators';
import { ExecutionDTO } from './model/executionDTO';
import { Process } from './model/process';
import { EventDTO } from './model/eventDTO';
import { Execution } from './model/execution';
import { YieldResult } from './model/yieldResult';


@Injectable({
  providedIn: 'root'
})
export class ExecutionService {
  constructor(private stateService: StateService, private trackingService: TrackingService, private http: HttpClient, private trackingWebsocketService: TrackingWebsocketService, private eventService: EventService, private eventWebsocketService: EventWebsocketService) { }


  getYieldResult(domainId: string, executionId: string, yieldResultId: string): Observable<YieldResult> {
    const url = `${PropertiesService.properties.fileServiceUrl}/execution/yieldResult/${domainId}/${executionId}/${yieldResultId}`;
    return this.http.get<YieldResult>(url);
  }

  getYieldResults(domainId: string, executionId: string): Observable<YieldResult[]> {
    const url = `${PropertiesService.properties.fileServiceUrl}/execution/yieldResult/${domainId}/${executionId}`;
    return this.http.get<YieldResult[]>(url);
  }


  getExecutionEventsByWs(domainId: string, processId: string, executionId: string, filter: any): Observable<EventDTO> {
    return this.eventWebsocketService.connect(filter).pipe(map((event: Event) => {
      if(event != null && event.domainId === domainId && event.processId === processId && event.executionId === executionId){
        return this.processAurionEvent(event);
      }
    })).pipe(retryWhen(errors =>
      errors.pipe(
        delay(1000)
      )))
  }


  getExecutionTracesByWs(domainId: string, processId: string, executionId: string, filter: any): Observable<ExecutionDTO> {
    return this.trackingWebsocketService.connect(filter).pipe(map((trace: any) => {
      const execution: Execution = trace as Execution;
      if (execution != null && execution.domainId === domainId && execution.processId === processId && execution.objectId === executionId) {
          return this.processAurionTrace(execution);
      }
    })).pipe(retryWhen(errors =>
      errors.pipe(
        delay(1000)
      )))
  }


  getExecutionsByWs(domainId: string, processId: string, filter: any): Observable<ExecutionDTO> {
    return this.trackingWebsocketService.connect(filter).pipe(map((trace: any) => {
      const execution: Execution = trace as Execution;
      if (execution != null && execution.domainId === domainId && execution.processId === processId) {
          return this.processAurionTrace(execution);
      }
    })).pipe(retryWhen(errors =>
      errors.pipe(
        delay(1000)
      )))
  }

  getExecutionTraces(domainId: string, executionId: string, startTimestamp: string): Observable<ExecutionDTO[]> {
    return this.trackingService.get(domainId, 'execution', executionId, startTimestamp).pipe(map((traces: Array<Trace>) => {
      return traces.map(item => this.processAurionTrace(item));
    }))
  }

  getExecutionTracesPoll(domainId: string, executionId: string, startTimestamp: string): Observable<ExecutionDTO[]> {
    return this.trackingService.get(domainId, 'execution', executionId, startTimestamp).pipe(map((traces: Array<Trace>) => {
      return traces.map(item => this.processAurionTrace(item));
    })).pipe(delay(15000)).pipe(repeat());
  }


  getExecutions(domainId: string, processId: string): Observable<ExecutionDTO[]> {
    return this.stateService.searchExecutionsByProcess(domainId, 'execution', processId).pipe(map((traces: Array<Trace>) => {
      return traces.map(item => this.processAurionTrace(item));
    }))
  }

  getExecutionsPoll(domainId: string, processId: string): Observable<ExecutionDTO[]> {
    return this.stateService.searchExecutionsByProcess(domainId, 'execution', processId).pipe(map((traces: Array<Trace>) => {
      return traces.map(item => this.processAurionTrace(item));
    })).pipe(delay(30000)).pipe(repeat());
  }

  closeWebsocket() {
    if (PropertiesService.properties.trackingwebsocket) {
      this.trackingWebsocketService.disconnect();
    }
  }

  getLatestEnded(processes: Process[]): Observable<ExecutionDTO[]> {
    return forkJoin(processes.map(process => this.getLastExecution(process.domainId, process.objectId)));
  }

  getLastExecution(domainId: string, id: string): Observable<ExecutionDTO> {
    return this.stateService.searchExecutionsByProcess(domainId, 'execution', id).pipe(map((states: Array<Trace>) => {
      if (states == null || states.length === 0) {
        return null;
      }
      return this.processAurionTrace(states[0]);
    }));
  }

  get(domainId: string, id: string): Observable<ExecutionDTO> {
    return this.stateService.get(domainId, 'execution', id).pipe(map((state) => {
      if (state == null) {
        return null;
      }
      return this.processAurionTrace(state);
    }));
  }

  getEvents(domainId: string, executionId: string, startTimestamp: number): Observable<EventDTO[]> {
    return this.eventService.getEventsBySubject(domainId, 'execution', startTimestamp, "executionId.eq:" + executionId).pipe(map( (events: Array<Event>) => {
      return events.map(event => this.processAurionEvent(event));
    }));
  }

  getEventsPoll(domainId: string, executionId: string, startTimestamp: number): Observable<EventDTO[]> {
    return this.eventService.getEventsBySubject(domainId, 'execution', startTimestamp, "executionId.eq:" + executionId).pipe(map( (events: Array<Event>) => {
      return events.map(event => this.processAurionEvent(event));
    })).pipe(delay(1000)).pipe(repeat());;
  }

  private processAurionTrace(trace: any): ExecutionDTO {
    let execution = trace as ExecutionDTO;

    execution.timestampStartPretty = trace.startedAt ? moment(trace.startedAt).format('DD/MM/YYYY HH:mm:ss') : undefined;
    execution.timestampFinalPretty = trace.finishedAt ? moment(trace.finishedAt).format('DD/MM/YYYY HH:mm:ss') : undefined;
    execution.timeEllapsedPretty = trace.executionTime ? (trace.executionTime / 1000) + 's' : null
    execution.timestampPretty = trace.timestamp ? moment(trace.timestamp).format('DD/MM/YYYY HH:mm:ss') : undefined;
    return execution;
  }

  private processAurionEvent(event: any): EventDTO {
    let executionEvent = event as EventDTO;
    executionEvent.timestampPretty = event.timestamp ? moment(event.timestamp).format('DD/MM/YYYY HH:mm:ss') : undefined;
    return executionEvent;
  }


}