import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { FileServiceMetadata } from './model/file-service-metadata';
import { PropertiesService } from '@alis/ng-base';
import { HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FileServiceMetadataService {

  constructor(private http: HttpClient) {}


  removeMetadata(domainId: string, fileObjectId: string, repository?: string): Observable<any> {
    let url = repository || PropertiesService.properties.fileServiceUrl + "/fileservice/file";
    let options = {
      headers: new HttpHeaders()
        .set("accept", "*/*"),
      params: new HttpParams()
        .set("domainId", domainId)
        .set("id", fileObjectId)
    };
    return this.http.delete(url, options);
  }

  removeFile(removeLink: string): Observable<any> {
    return this.http.delete(removeLink);
  }

  uploadMetadata(newFileServiceMetadata: FileServiceMetadata, repository?: string): Observable<any> {
    let url = repository || PropertiesService.properties.fileServiceUrl + "/fileservice/file";
    let options = {
      headers: new HttpHeaders()
      .set("accept", "*/*")
      .set("content-type", "application/json")
    };
    return this.http.post<FileServiceMetadata>(url, newFileServiceMetadata, options);
  }

  /*
    obs: the headers received by this method, comes in this format:
    {
      <header1>: <header1 value>,
      <header2>: <header2 value>
      .
      .
      .
    }

    when receiving headers as HttpHeaders, built by the service consumer, 
    the headers that were added goes to "lazyUpdate" field inside HttpHeaders
    instance instead "headers" field, which leads to CORS error due those mandatory
    headers are not been sent.

    in order to solve this, the "headers" object, demanded by HttpClient methods, 
    accepts a plain JSON object. therefore, the service consumer should send
    "headers" as a plain JSON object.
  */
  uploadFile(uploadLink: string, file: File, headers: any): Observable<any> { 
    let options = {
      headers: headers
    }
    return this.http.put<any>(uploadLink, file, options);
  }

  //TODO - verificar se existe uma forma de jÃ¡ buscar os metadados ordenados atravÃ©s do indexing;
  //o mesmo vale para processos e execuÃ§Ãµes; enquanto isso, o front ordena as listas quando necessÃ¡rio
  /**
   * 
   * @param domainId 
   * @param path 
   * @param repository 
   * @param query HTTP List of filters using the Aurion SearchCriteria pattern: key.operation:filterValue[,otherFilterValue]. Operations: eq/neq/sw/gt/egt/lt/elt. (ex: attribute1.eq:testValue)
   */
  list(domainId: string, path: string, strictly?: boolean, repository?: string, query?: string ): Observable<Array<FileServiceMetadata>> {
    let url = repository || PropertiesService.properties.fileServiceUrl + "/fileservice/list";


    let params =  new HttpParams()
    .set("domainId", domainId)
    .set("path", path)
    .set("locationStrictly", strictly != null ? "" + strictly : "false");

    if(query != null){
      params = params.set('query',query);
    }

    let options = {
      headers: new HttpHeaders().set("accept", "*/*"),
      params: params
    };
    return this.http.get<Array<FileServiceMetadata>>(url, options).pipe(
      map( (fileServiceMetadatas: Array<FileServiceMetadata>) => {
        return fileServiceMetadatas.sort((a: FileServiceMetadata, b: FileServiceMetadata) => {
          return a.creationDateTimestamp > b.creationDateTimestamp ? -1 : 1;
        })
      })
    );
  }

  getFileDetail(domainId: string, fileId: string, repository?: string): Observable<any> {
    let url = repository || PropertiesService.properties.fileServiceUrl + "/fileservice/file";
    let options = {
      headers: new HttpHeaders()
        .set("accept", "*/*"),
      params: new HttpParams()
        .set("domainId", domainId)
        .set("id", fileId)
    };
    return this.http.get(url, options);
  }

  downloadFile(fileMetadata: FileServiceMetadata): void {
    this.getFileDetail(fileMetadata.domainId, fileMetadata.objectId).subscribe( fileDetail => {
      if (fileDetail != null) {
        const link = fileDetail.metadata.link ? fileDetail.metadata.link : fileDetail['link'];
        const metadata: FileServiceMetadata = fileDetail['metadata'];
        if(metadata == null) {
          console.warn('metadata received is null');
          return null;
        }
        let fileName = metadata.name;
        this.download(link,fileName);
      } else {
        console.error("response is null", fileDetail);
      }

    }, (error) => {
      console.error(error);
    });
  }

  async download(url, fileName) {
    const a = document.createElement("a");
    a.href = await this.toDataURL(url);
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
  }

  toDataURL(url) {
    return fetch(url).then((response) => {
      return response.blob();
    }).then(blob => {
      return URL.createObjectURL(blob);
    });
  }

  getBytes(fileId: string, repository?: string): Observable<ArrayBuffer> {
    return null;
  }

  // getRootFolder(): StorableFolder {
  //   return this.rootFolder;
  // }
}
