import { PropertiesService, TrackingService } from '@alis/ng-base';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map} from 'rxjs/operators';
import { Process } from './model/process';

@Injectable({
  providedIn: 'root'
})
export class ProcessService {

  constructor(private trackingService: TrackingService, private http: HttpClient) { }

  /*
  cadu  3:15 PM
  na doc da modelagem do workflows eu tive q adicionar na ExecuÃ§Ã£o 2 campos, o context e location
  context Ã© o cliente (AES) e location Ã© o processo
  */

  get(domainId: string): Observable<Process[]> {
    return this.trackingService.getByType(domainId, 'process').pipe(map((traces: Process[]) => {
      return traces.map(trace => {
        return trace as Process;
      })
    }))
  }

  getById(domainId: string, processId: string): Observable<Process> {
    return this.get(domainId).pipe(map(items => {
      return items.find(item => item.objectId == processId);
    }))
  }

  save(process: Process): Observable<any> {
    return this.trackingService.set(process);
  }

  execute(domainId: string, processObjectId: string, inputs: { code: string, value: any }[], executionMode: string = "default"): Observable<any> {

    let url = `${PropertiesService.properties.fileServiceUrl}/execution/start/${domainId}/${processObjectId}`;
    let userJSON = sessionStorage.getItem('user');
    let user = userJSON != null && userJSON != '' ? JSON.parse(userJSON) : "";

    // let inputsPostFormat = inputs.reduce((prev, cur) => { prev[cur.id] = cur.value; return prev }, {});

    return this.http.post(url, { processId: processObjectId, inputs: inputs, executionMode: executionMode },
      {
        headers: {
          'Authorization': 'Bearer ' + user.idToken 
        }
      });
  }

  continue(domainId: string, executionId: string, inputs: { id: string, value: any }[]): Observable<any> {
    let url = `${PropertiesService.properties.fileServiceUrl}/execution/${domainId}/${executionId}/continue`;

    let userJSON = sessionStorage.getItem('user');
    const parsedUserJSON = userJSON != null && userJSON != '' ? JSON.parse(userJSON) : "";

    let payload = {
      domainId: domainId,
      executionId: executionId,
      inputs: inputs,
      user: parsedUserJSON.idTokenPayload?.nickname || "",
    };

    return this.http.post(url, payload, 
    {
      headers: {
        'Authorization': 'Bearer ' + parsedUserJSON.idToken
    },
      responseType: 'text'
    });
  }
  
  //continue overriding stageIndex
  continueOverridingStage(domainId: string, executionId: string, inputs: { id: string, value: any }[], overrideStageIndex: number): Observable<any> {
    let url = `${PropertiesService.properties.fileServiceUrl}/execution/${domainId}/${executionId}/continue?overrideStageIndex=${overrideStageIndex}`;

    let userJSON = sessionStorage.getItem('user');
    const parsedUserJSON = userJSON != null && userJSON != '' ? JSON.parse(userJSON) : "";

    let payload = {
      inputs: inputs
    };

    return this.http.post(url, payload, 
    {
      headers: {
        'Authorization': 'Bearer ' + parsedUserJSON.idToken
    },
      responseType: 'text'
    });
  }
}
